import React from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter as Router, Routes, Route, Navigate } from 'react-router-dom';
import SignInSide from './SignInSide'; // ng dn tng i n file SignInSide.js
import App from './App'; // ng dn tng i n file App.js


ReactDOM.render(
  <React.StrictMode>
    <Router>
      <Routes>
        {/* nh ngha Route cho trang ng nhp */}
        <Route path="/signin" element={<SignInSide />} />
        {/* Route cho trang chnh */}
        <Route path="/app" element={<App />} />
        {/* ng dn mc nh s chuyn hng n trang ng nhp */}
        <Route path="/" element={<Navigate to="/signin" />} />
      </Routes>
    </Router>
  </React.StrictMode>,
  document.getElementById('root')
);

// hoc s dng ReactDOM.createRoot:
const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(
  <React.StrictMode>
    <Router>
      <Routes>
        {/* nh ngha Route cho trang ng nhp */}
        <Route path="/signin" element={<SignInSide />} />
        {/* Route cho trang chnh */}
        <Route path="/app" element={<App />} />
        {/* ng dn mc nh s chuyn hng n trang ng nhp */}
        <Route path="/" element={<Navigate to="/signin" />} />
      </Routes>
    </Router>
  </React.StrictMode>
);
